.hero{
    &__container{
        padding-top: 100px;
        padding-bottom: 100px;
        background-image: url(../img/hero-bg.png);
        background-position: calc(100% + 85px) 0;
        background-repeat: no-repeat;
    }
    &__sub-title{
        margin: 0;
        margin-bottom: 35px;
        font-weight: 700;
        font-size: 30px;
        line-height: 35/30 * 100%;
        color: $color-green;
    }
    &__title{
        max-width: 600px;
        margin: 0;
        margin-bottom: 35px;
        font-weight: 900;
        font-size: 50px;
        line-height: 59/50 * 100%;
    }
    &__descr{
        max-width: 600px;
        margin: 0;
        margin-bottom: 60px;
        font-style: italic;
        font-size: 25px;
        line-height: 29/25 * 100%;
    }
    &__link{
        display: inline-block;
        padding: 15px 80px;
        background-color: $color-green;
        font-weight: 700;
        font-size: 18px;
        line-height: 21/18 * 100%;
        text-transform: uppercase;
        color: $color-white;
        transition: transform .3s ease 0s, box-shadow .3s ease 0s;
        &:hover{
            transform: scale(105%);
            box-shadow: 0 0 10px rgba($color-black, .4);
        }
    }
}