.header{
    &__container{
        display: flex;
        align-items: center;
        padding-top: 60px;
        padding-bottom: 60px;
    }
    &__logo{
        flex: 0 0 410px;
    }
    &__phone{
        font-weight: 700;
        font-size: 20px;
        line-height: 23/20 * 100%;
        color: inherit;
        transition: color .3s ease 0s;
        &:hover{
            color: $color-green;
        }
    }
}
.menu{
    flex: 1 0 600px;
    &__list{
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;
    }
    &__item{
        &:not(:last-child){
            margin-right: 60px;
        }
    }
    &__link{
        font-weight: 700;
        font-size: 20px;
        line-height: 23/20 * 100%;
        color: inherit;
        transition: color .3s ease 0s;
        &:hover{
            color: $color-green;
        }
    }
}
.icon-menu{
    display: none;
}