.prices{
    &__container{
        padding-top: 100px;
        padding-bottom: 100px;
    }
    &__title{
        max-width: 1190px;
        margin-bottom: 75px;
        font-weight: 500;
        &--bold{
            font-weight: 700;
            color: $color-green;
        }
    }
    &__list{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        list-style: none;
        margin: 0;
        padding: 0;
    }
}
.prices-item{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 425px;
    padding: 40px 30px;
    box-shadow: 0px 4px 10px rgba($color-black, .25);
    &__title{
        margin: 0;
        margin-bottom: 10px;
        font-weight: 700;
        font-size: 30px;
        line-height: 35/30 * 100%;
    }
    &__descr{
        margin: 0;
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 20px;
        line-height: 23/20 * 100%;
        &--green{
            margin-bottom: 40px;
            color: $color-green;
        }
    }
    &__img{
        margin-bottom: 40px;
    }
    &__inner{
        margin-bottom: 40px;
        &--other{
            margin-bottom: 55px;
        }
    }
    &__paragraph{
        margin: 0;
        font-weight: 500;
        font-size: 18px;
        line-height: 21/18 * 100%;
        &:not(:last-child){
            margin-bottom: 10px;
        }
    }
    &__sub-title{
        margin: 0;
        margin-bottom: 50px;
        font-weight: 700;
        font-size: 20px;
        line-height: 23/20 * 100%;
    }
    &__btn{
        padding: 15px 65px;
        background-color: $color-green;
        font-weight: 700;
        font-size: 18px;
        line-height: 21/18 * 100%;
        text-transform: uppercase;
        color: $color-white;
        transition: transform .3s ease 0s, box-shadow .3s ease 0s;
        &:hover{
            transform: scale(105%);
            box-shadow: 0 0 10px rgba($color-black, .4);
        }
    }
}