.arrangement{
    &__container{
        padding-top: 100px;
        padding-bottom: 100px;
    }
    &__title{
        margin-bottom: 10px;
        font-weight: 500;
        &--bold{
            font-size: 700;
            color: $color-green;
        }
    }
    &__descr{
        margin: 0;
        margin-bottom: 100px;
        font-style: italic;
        font-size: 30px;
        line-height: 35/30 * 100%;
    }
    &__sub-title{
        margin: 0;
        margin-bottom: 100px;
        font-weight: 700;
        font-size: 30px;
        line-height: 35/30 * 100%;
        text-align: center;
    }
    &__list{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 20px;
        list-style: none;
        margin: 0 0 100px;
        padding: 0;
    }
    &__text{
        max-width: 1050px;
        margin: 0 auto;
        font-weight: 500;
        font-size: 25px;
        line-height: 29/25 * 100%;
        text-align: center;
    }
    &__link{
        color: $color-green;
    }
}
.arrangement-item{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    max-width: 700px;
    padding: 30px;
    box-shadow: 0px 4px 10px rgba($color-black, .25);
    &--other{
        padding: 30px;
        box-shadow: none;
    }
    &__container{
        display: flex;
        align-items: center;
        margin-bottom: 40px;
    }
    &__content{
        padding: 0 60px;
    }
    &__title{
        margin: 0;
        margin-bottom: 10px;
        font-weight: 400;
        font-weight: 500;
        font-size: 30px;
        line-height: 35/30 * 100%;
        &--other{
            margin-bottom: 30px;
        }
    }
    &__descr{
        margin: 0;
        font-size: 18px;
        line-height: 21/18 * 100%;
    }
    &__inner{
        margin-bottom: 40px;
        &--bold{
            margin-bottom: 80px;
        }
        &--other{
            margin-bottom: 60px;
        }
    }
    &__paragraph{
        margin: 0;
        font-size: 18px;
        line-height: 21/18 * 100%;
        &--bold{
            font-weight: 700;
        }
        &--other{
            font-size: 25px;
            line-height: 29/25 * 100%;
        }
        &:not(:last-child){
            margin-bottom: 20px;
        }
    }
    &__sub-title{
        margin: 0;
        margin-bottom: 20px;
        font-weight: 700;
        font-size: 20px;
        line-height: 23/20 * 100%;
        &--other{
            font-weight: 400;
            font-size: 25px;
            line-height: 29/25 * 100%;
            color: $color-green;
        }
    }
    &__btn{
        align-self: center;
        padding: 15px 90px;
        background-color: $color-green;
        font-weight: 700;
        font-size: 18px;
        line-height: 21/18 * 100%;
        color: $color-white;
        transition: transform .3s ease 0s, box-shadow .3s ease 0s;
        &:hover{
            transform: scale(105%);
            box-shadow: 0 0 10px rgba($color-black, .4);
        }
    }
}