.benefits{
    padding-top: 100px;
    padding-bottom: 100px;
    &__title{
        max-width: 1240px;
        margin-bottom: 120px;
        font-weight: 500;
        &--bold{
            font-weight: 700;
            color: $color-green;
        }
    }
    &__inner{
        padding-bottom: 40px;
        background-color: $color-green;
    }
    &__list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 365px;
        grid-row-gap: 165px;
        list-style: none;
        margin: 0;
        padding: 0;
    }
}
.benefits-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 230px;
    margin-top: -80px;
    text-align: center;
    &__circle{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 160px;
        height: 160px;
        border-radius: 50%;
        margin-bottom: 25px;
        background-color: $color-white;
    }
    &__descr{
        margin: 0;
        font-weight: 700;
        font-size: 18px;
        line-height: 21/18 * 100%;
        color: $color-white;
    }
}