.quiz{
    margin-bottom: 100px;
    &__inner{
        padding: 40px 140px;
        background-color: $color-gray-light;
        box-shadow: 0px 4px 4px rgba($color-black, .25);
    }
    &__title{
        margin: 0;
        margin-bottom: 50px;
        font-weight: 700;
        font-size: 30px;
        line-height: 35/30 * 100%;
        text-align: center;
    }
}
.quiz-form{
    &__input{
        width: 100%;
        height: 50px;
        border: 0;
        margin-bottom: 25px;
        padding: 0 60px;
        background-color: $color-white;
        font-style: italic;
        font-weight: 300;
        font-size: 18px;
        line-height: 21/18 * 100%;
        color: $color-black;
        &::placeholder{
            font-style: italic;
            font-weight: 300;
            font-size: 18px;
            line-height: 21/18 * 100%;
            color: $color-gray;
        }
    }
    &__inner{
        display: flex;
        align-items: center;
    }
    &__descr{
        flex: 1 0 auto;
    }
    &__btn{
        padding: 15px 65px;
        background-color: $color-green;
        font-size: 18px;
        line-height: 21/18 * 100%;
        color: $color-white;
    }
}