.other-services{
    &__title{
        margin-bottom: 100px;
        font-weight: 700;
    }
    &__list{
        list-style: none;
        margin: 0;
        padding: 0;
    }
}
.other-services-item{
    display: flex;
    align-items: center;
    margin-bottom: 70px;
    padding: 40px;
    box-shadow: 0px 4px 10px rgba($color-black, .25);
    &__img{
        flex: 0 1 180px;
        margin-right: 120px;
    }
    &__title{
        margin: 0;
        margin-bottom: 25px;
        font-weight: 500;
        font-size: 30px;
        line-height: 35/30 * 100%;
    }
    &__descr{
        margin: 0;
        margin-bottom: 55px;
        font-size: 25px;
        line-height: 29/25 * 100%;
    }
    &__btn{
        padding: 20px 25px;
        background-color: $color-green;
        font-size: 18px;
        line-height: 21/18 * 100%;
        text-transform: uppercase;
        color: $color-white;
        transition: transform .3s ease 0s, box-shadow .3s ease 0s;
        &:hover{
            transform: scale(105%);
            box-shadow: 0 0 10px rgba($color-black, .4);
        }
    }
}