.advice{
    padding-top: 100px;
    padding-bottom: 100px;
    &__inner{
        padding: 40px 140px;
        background-color: $color-gray-light;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    &__title{
        margin: 0;
        margin-bottom: 35px;
        font-weight: 700;
        font-size: 30px;
        line-height: 35/30 * 100%;
        text-align: center;
    }
}
.advice-form{
    display: flex;
    align-items: flex-start;
    &__main{
        flex: 1 0 auto;
    }
    &__inner{
        display: flex;
        margin-bottom: 30px;
    }
    &__input{
        width: 270px;
        height: 50px;
        border: 0;
        padding: 0 10px;
        background-color: $color-white;
        font-style: italic;
        font-weight: 300;
        font-size: 18px;
        line-height: 21/18 * 100%;
        text-align: center;
        color: $color-black;
        &:not(:last-child){
            margin-right: 30px;
        }
        &::placeholder{
            font-style: italic;
            font-weight: 300;
            font-size: 18px;
            line-height: 21/18 * 100%;
            color: $color-gray;
        }
    }
    &__descr{
        margin: 0;
        font-style: italic;
        font-size: 15px;
        line-height: 18/15 * 100%;
        color: $color-gray;
    }
    &__btn{
        padding: 15px 30px;
        background-color: $color-green;
        font-weight: 700;
        font-size: 18px;
        line-height: 21/18 * 100%;
        color: $color-white;
        transition: transform .3s ease 0s, box-shadow .3s ease 0s;
        &:hover{
            transform: scale(105%);
            box-shadow: 0 0 10px rgba($color-black, .4);
        }
    }
}