.services{
    &__container{
        padding-top: 100px;
        padding-bottom: 100px;
    }
    &__list{
        display: flex;
        justify-content: space-between;
        list-style: none;
        margin: 0;
        padding: 0;
    }
}
.services-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 345px;
    padding: 30px 20px;
    text-align: center;
    transition: transform .3s ease 0s, box-shadow .3s ease 0s;
    &:hover{
        transform: translateY(-5px);
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    }
    &__img{
        margin-bottom: 20px;
    }
    &__title{
        margin: 0;
        margin-bottom: 20px;
        font-weight: 500;
        font-size: 30px;
        line-height: 35/30 * 100%;
    }
    &__descr{
        margin: 0;
        font-size: 20px;
        line-height: 23/20 * 100%;
    }
}