html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}

img {
  display: block;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  max-width: 1450px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn-reset {
  border: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.title-default {
  margin: 0;
  font-size: 45px;
  line-height: 117.77778%;
}

.header__container {
  display: flex;
  align-items: center;
  padding-top: 60px;
  padding-bottom: 60px;
}

.header__logo {
  flex: 0 0 410px;
}

.header__phone {
  font-weight: 700;
  font-size: 20px;
  line-height: 115%;
  color: inherit;
  transition: color .3s ease 0s;
}

.header__phone:hover {
  color: #00a155;
}

.menu {
  flex: 1 0 600px;
}

.menu__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu__item:not(:last-child) {
  margin-right: 60px;
}

.menu__link {
  font-weight: 700;
  font-size: 20px;
  line-height: 115%;
  color: inherit;
  transition: color .3s ease 0s;
}

.menu__link:hover {
  color: #00a155;
}

.icon-menu {
  display: none;
}

.hero__container {
  padding-top: 100px;
  padding-bottom: 100px;
  background-image: url(../img/hero-bg.png);
  background-position: calc(100% + 85px) 0;
  background-repeat: no-repeat;
}

.hero__sub-title {
  margin: 0;
  margin-bottom: 35px;
  font-weight: 700;
  font-size: 30px;
  line-height: 116.66667%;
  color: #00a155;
}

.hero__title {
  max-width: 600px;
  margin: 0;
  margin-bottom: 35px;
  font-weight: 900;
  font-size: 50px;
  line-height: 118%;
}

.hero__descr {
  max-width: 600px;
  margin: 0;
  margin-bottom: 60px;
  font-style: italic;
  font-size: 25px;
  line-height: 116%;
}

.hero__link {
  display: inline-block;
  padding: 15px 80px;
  background-color: #00a155;
  font-weight: 700;
  font-size: 18px;
  line-height: 116.66667%;
  text-transform: uppercase;
  color: #fff;
  transition: transform .3s ease 0s, box-shadow .3s ease 0s;
}

.hero__link:hover {
  transform: scale(105%);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.advice {
  padding-top: 100px;
  padding-bottom: 100px;
}

.advice__inner {
  padding: 40px 140px;
  background-color: #e1e1e1;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.advice__title {
  margin: 0;
  margin-bottom: 35px;
  font-weight: 700;
  font-size: 30px;
  line-height: 116.66667%;
  text-align: center;
}

.advice-form {
  display: flex;
  align-items: flex-start;
}

.advice-form__main {
  flex: 1 0 auto;
}

.advice-form__inner {
  display: flex;
  margin-bottom: 30px;
}

.advice-form__input {
  width: 270px;
  height: 50px;
  border: 0;
  padding: 0 10px;
  background-color: #fff;
  font-style: italic;
  font-weight: 300;
  font-size: 18px;
  line-height: 116.66667%;
  text-align: center;
  color: #000;
}

.advice-form__input:not(:last-child) {
  margin-right: 30px;
}

.advice-form__input::placeholder {
  font-style: italic;
  font-weight: 300;
  font-size: 18px;
  line-height: 116.66667%;
  color: #656565;
}

.advice-form__descr {
  margin: 0;
  font-style: italic;
  font-size: 15px;
  line-height: 120%;
  color: #656565;
}

.advice-form__btn {
  padding: 15px 30px;
  background-color: #00a155;
  font-weight: 700;
  font-size: 18px;
  line-height: 116.66667%;
  color: #fff;
  transition: transform .3s ease 0s, box-shadow .3s ease 0s;
}

.advice-form__btn:hover {
  transform: scale(105%);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.services__container {
  padding-top: 100px;
  padding-bottom: 100px;
}

.services__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
}

.services-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 345px;
  padding: 30px 20px;
  text-align: center;
  transition: transform .3s ease 0s, box-shadow .3s ease 0s;
}

.services-item:hover {
  transform: translateY(-5px);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

.services-item__img {
  margin-bottom: 20px;
}

.services-item__title {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 30px;
  line-height: 116.66667%;
}

.services-item__descr {
  margin: 0;
  font-size: 20px;
  line-height: 115%;
}

.benefits {
  padding-top: 100px;
  padding-bottom: 100px;
}

.benefits__title {
  max-width: 1240px;
  margin-bottom: 120px;
  font-weight: 500;
}

.benefits__title--bold {
  font-weight: 700;
  color: #00a155;
}

.benefits__inner {
  padding-bottom: 40px;
  background-color: #00a155;
}

.benefits__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 365px;
  grid-row-gap: 165px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.benefits-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 230px;
  margin-top: -80px;
  text-align: center;
}

.benefits-item__circle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  margin-bottom: 25px;
  background-color: #fff;
}

.benefits-item__descr {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 116.66667%;
  color: #fff;
}

.prices__container {
  padding-top: 100px;
  padding-bottom: 100px;
}

.prices__title {
  max-width: 1190px;
  margin-bottom: 75px;
  font-weight: 500;
}

.prices__title--bold {
  font-weight: 700;
  color: #00a155;
}

.prices__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}

.prices-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 425px;
  padding: 40px 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

.prices-item__title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 30px;
  line-height: 116.66667%;
}

.prices-item__descr {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 20px;
  line-height: 115%;
}

.prices-item__descr--green {
  margin-bottom: 40px;
  color: #00a155;
}

.prices-item__img {
  margin-bottom: 40px;
}

.prices-item__inner {
  margin-bottom: 40px;
}

.prices-item__inner--other {
  margin-bottom: 55px;
}

.prices-item__paragraph {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 116.66667%;
}

.prices-item__paragraph:not(:last-child) {
  margin-bottom: 10px;
}

.prices-item__sub-title {
  margin: 0;
  margin-bottom: 50px;
  font-weight: 700;
  font-size: 20px;
  line-height: 115%;
}

.prices-item__btn {
  padding: 15px 65px;
  background-color: #00a155;
  font-weight: 700;
  font-size: 18px;
  line-height: 116.66667%;
  text-transform: uppercase;
  color: #fff;
  transition: transform .3s ease 0s, box-shadow .3s ease 0s;
}

.prices-item__btn:hover {
  transform: scale(105%);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.arrangement__container {
  padding-top: 100px;
  padding-bottom: 100px;
}

.arrangement__title {
  margin-bottom: 10px;
  font-weight: 500;
}

.arrangement__title--bold {
  font-size: 700;
  color: #00a155;
}

.arrangement__descr {
  margin: 0;
  margin-bottom: 100px;
  font-style: italic;
  font-size: 30px;
  line-height: 116.66667%;
}

.arrangement__sub-title {
  margin: 0;
  margin-bottom: 100px;
  font-weight: 700;
  font-size: 30px;
  line-height: 116.66667%;
  text-align: center;
}

.arrangement__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  list-style: none;
  margin: 0 0 100px;
  padding: 0;
}

.arrangement__text {
  max-width: 1050px;
  margin: 0 auto;
  font-weight: 500;
  font-size: 25px;
  line-height: 116%;
  text-align: center;
}

.arrangement__link {
  color: #00a155;
}

.arrangement-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 700px;
  padding: 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

.arrangement-item--other {
  padding: 30px;
  box-shadow: none;
}

.arrangement-item__container {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.arrangement-item__content {
  padding: 0 60px;
}

.arrangement-item__title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 400;
  font-weight: 500;
  font-size: 30px;
  line-height: 116.66667%;
}

.arrangement-item__title--other {
  margin-bottom: 30px;
}

.arrangement-item__descr {
  margin: 0;
  font-size: 18px;
  line-height: 116.66667%;
}

.arrangement-item__inner {
  margin-bottom: 40px;
}

.arrangement-item__inner--bold {
  margin-bottom: 80px;
}

.arrangement-item__inner--other {
  margin-bottom: 60px;
}

.arrangement-item__paragraph {
  margin: 0;
  font-size: 18px;
  line-height: 116.66667%;
}

.arrangement-item__paragraph--bold {
  font-weight: 700;
}

.arrangement-item__paragraph--other {
  font-size: 25px;
  line-height: 116%;
}

.arrangement-item__paragraph:not(:last-child) {
  margin-bottom: 20px;
}

.arrangement-item__sub-title {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 20px;
  line-height: 115%;
}

.arrangement-item__sub-title--other {
  font-weight: 400;
  font-size: 25px;
  line-height: 116%;
  color: #00a155;
}

.arrangement-item__btn {
  align-self: center;
  padding: 15px 90px;
  background-color: #00a155;
  font-weight: 700;
  font-size: 18px;
  line-height: 116.66667%;
  color: #fff;
  transition: transform .3s ease 0s, box-shadow .3s ease 0s;
}

.arrangement-item__btn:hover {
  transform: scale(105%);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.stages__container {
  padding-top: 100px;
  padding-bottom: 100px;
}

.stages__title {
  margin-bottom: 20px;
  font-weight: 500;
}

.stages__title--bold {
  font-weight: 700;
  color: #00a155;
}

.stages__descr {
  margin: 0;
  margin-bottom: 150px;
  font-style: italic;
  font-size: 30px;
  line-height: 116.66667%;
}

.stages__descr--link {
  color: #00a155;
}

.stages__inner {
  position: relative;
  width: 100%;
}

.stages__inner::before {
  content: "";
  position: absolute;
  top: 37px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #e1e1e1;
}

.stages__list {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  list-style: none;
  margin: 0;
  padding: 0;
}

.stages-item {
  position: relative;
  z-index: 5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 230px;
  text-align: center;
}

.stages-item__circle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 74px;
  height: 74px;
  border-radius: 50%;
  margin-bottom: 35px;
  background-color: #00a155;
}

.stages-item__number {
  font-weight: 700;
  font-size: 25px;
  line-height: 116%;
  color: #fff;
}

.stages-item__title {
  margin: 0;
  margin-bottom: 5px;
  font-weight: 500;
  font-size: 25px;
  line-height: 116%;
}

.stages-item__descr {
  margin: 0;
  font-size: 18px;
  line-height: 116.66667%;
}

.quiz {
  margin-bottom: 100px;
}

.quiz__inner {
  padding: 40px 140px;
  background-color: #e1e1e1;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.quiz__title {
  margin: 0;
  margin-bottom: 50px;
  font-weight: 700;
  font-size: 30px;
  line-height: 116.66667%;
  text-align: center;
}

.quiz-form__input {
  width: 100%;
  height: 50px;
  border: 0;
  margin-bottom: 25px;
  padding: 0 60px;
  background-color: #fff;
  font-style: italic;
  font-weight: 300;
  font-size: 18px;
  line-height: 116.66667%;
  color: #000;
}

.quiz-form__input::placeholder {
  font-style: italic;
  font-weight: 300;
  font-size: 18px;
  line-height: 116.66667%;
  color: #656565;
}

.quiz-form__inner {
  display: flex;
  align-items: center;
}

.quiz-form__descr {
  flex: 1 0 auto;
}

.quiz-form__btn {
  padding: 15px 65px;
  background-color: #00a155;
  font-size: 18px;
  line-height: 116.66667%;
  color: #fff;
}

.works {
  margin-bottom: 100px;
}

.works__title {
  margin-bottom: 70px;
  font-weight: 500;
}

.works__title--bold {
  font-weight: 700;
  color: #00a155;
}

.works__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 200px;
}

.works__quote {
  position: relative;
  max-width: 645px;
  height: auto;
  margin: 0;
  padding: 30px 30px 30px 110px;
  background-color: #e1e1e1;
  font-style: italic;
  font-size: 25px;
  line-height: 116%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.works__quote::before {
  content: "";
  position: absolute;
  top: 30px;
  left: 30px;
  width: 46px;
  height: 46px;
  background-image: url(../img/quote.png);
}

.works__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.works__text {
  margin: 0;
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 25px;
  line-height: 116%;
}

.works__text--office {
  font-weight: 500;
  margin-bottom: 0;
}

.works__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
}

.works__item {
  position: relative;
  width: 100%;
  max-width: 440px;
  padding: 50px 25px;
  background-color: #00a155;
}

.works__item:nth-child(1)::before {
  content: "";
  position: absolute;
  z-index: -1;
  right: -170px;
  bottom: -185px;
  width: 334px;
  height: 286px;
  background-image: url(../img/arrow.png);
  transform: scale(1, -1);
}

.works__item:nth-child(2)::before {
  content: "";
  position: absolute;
  z-index: -1;
  right: -170px;
  bottom: 85px;
  width: 334px;
  height: 286px;
  background-image: url(../img/arrow.png);
}

.works__item-descr {
  margin: 0;
  font-size: 25px;
  line-height: 116%;
  color: #fff;
}

.other-services__title {
  margin-bottom: 100px;
  font-weight: 700;
}

.other-services__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.other-services-item {
  display: flex;
  align-items: center;
  margin-bottom: 70px;
  padding: 40px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

.other-services-item__img {
  flex: 0 1 180px;
  margin-right: 120px;
}

.other-services-item__title {
  margin: 0;
  margin-bottom: 25px;
  font-weight: 500;
  font-size: 30px;
  line-height: 116.66667%;
}

.other-services-item__descr {
  margin: 0;
  margin-bottom: 55px;
  font-size: 25px;
  line-height: 116%;
}

.other-services-item__btn {
  padding: 20px 25px;
  background-color: #00a155;
  font-size: 18px;
  line-height: 116.66667%;
  text-transform: uppercase;
  color: #fff;
  transition: transform .3s ease 0s, box-shadow .3s ease 0s;
}

.other-services-item__btn:hover {
  transform: scale(105%);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
