.works{
    margin-bottom: 100px;
    &__title{
        margin-bottom: 70px;
        font-weight: 500;
        &--bold{
            font-weight: 700;
            color: $color-green;
        }
    }
    &__inner{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 200px;
    }
    &__quote{
        position: relative;
        max-width: 645px;
        height: auto;
        margin: 0;
        padding: 30px 30px 30px 110px;
        background-color: $color-gray-light;
        font-style: italic;
        font-size: 25px;
        line-height: 29/25 * 100%;
        box-shadow: 0px 4px 4px rgba($color-black, .25);
        &::before{
            content: "";
            position: absolute;
            top: 30px;
            left: 30px;
            width: 46px;
            height: 46px;
            background-image: url(../img/quote.png);
        }
    }
    &__content{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    &__text{
        margin: 0;
        margin-bottom: 5px;
        font-weight: 700;
        font-size: 25px;
        line-height: 29/25 * 100%;
        &--office{
            font-weight: 500;
            margin-bottom: 0;
        }
    }
    &__list{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        list-style: none;
        margin: 0;
        padding: 0;
    }
    &__item{
        position: relative;
        width: 100%;
        max-width: 440px;
        padding: 50px 25px;
        background-color: $color-green;
        &:nth-child(1){
            &::before{
                content: "";
                position: absolute;
                z-index: -1;
                right: -170px;
                bottom: -185px;
                width: 334px;
                height: 286px;
                background-image: url(../img/arrow.png);
                transform: scale(1, -1);
            }
        }
        &:nth-child(2){
            &::before{
                content: "";
                position: absolute;
                z-index: -1;
                right: -170px;
                bottom: 85px;
                width: 334px;
                height: 286px;
                background-image: url(../img/arrow.png);
            }
        }
    }
    &__item-descr{
        margin: 0;
        font-size: 25px;
        line-height: 29/25 * 100%;
        color: $color-white;
    }
}