.stages{
    &__container{
        padding-top: 100px;
        padding-bottom: 100px;
    }
    &__title{
        margin-bottom: 20px;
        font-weight: 500;
        &--bold{
            font-weight: 700;
            color: $color-green;
        }
    }
    &__descr{
        margin: 0;
        margin-bottom: 150px;
        font-style: italic;
        font-size: 30px;
        line-height: 35/30 * 100%;
        &--link{
            color: $color-green;
        }
    }
    &__inner{
        position: relative;
        width: 100%;
        &::before{
            content: "";
            position: absolute;
            top: 37px;
            left: 0;
            width: 100%;
            height: 3px;
            background-color: $color-gray-light;
        }
    }
    &__list{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        list-style: none;
        margin: 0;
        padding: 0;
    }
}
.stages-item{
    position: relative;
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 230px;
    text-align: center;
    &__circle{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 74px;
        height: 74px;
        border-radius: 50%;
        margin-bottom: 35px;
        background-color: $color-green;
    }
    &__number{
        font-weight: 700;
        font-size: 25px;
        line-height: 29/25 * 100%;
        color: $color-white;
    }
    &__title{
        margin: 0;
        margin-bottom: 5px;
        font-weight: 500;
        font-size: 25px;
        line-height: 29/25 * 100%;
    }
    &__descr{
        margin: 0;
        font-size: 18px;
        line-height: 21/18 * 100%;
    }
}